import React, { useEffect, useState } from 'react';
import {Line} from 'react-chartjs-2'
import Chart from 'chart.js/auto'
import Select from 'react-select'

const Linegraph=(props)=> {
    const {backbutton,name,rotat}=props;
    const [time,settime]=useState([]);
    const [price,setprice]=useState([]);
    const [inputtime,setinputtime]=useState('m30');
    const options = [
        { value: 'm1', label: '1 Min' },
        { value: 'm5', label: '5 Min' },
        { value: 'm15', label: '15 Min' },
        { value: 'h1', label: '1 Hour' },
        { value: 'd1', label: '1 Day' },
    ]
    const data={
        labels:[...time],
        datasets:[
            {
                label:"Price",
               data:[...price],
               pointRadius:0,
               borderColor:['rgba(21, 13, 40, 0.8)'],
               tension:0.4
            }
        ]
    }

    useEffect(()=>{
        fetch(`https://api.coincap.io/v2/assets/${name}/history?interval=${inputtime}`)
        .then(response=>response.json())
        .then(data=>{
            const data1=data.data;
            const ti=[];
            const pri=[];
            data1.forEach((ele)=>{
                ti.push(ele.date.split('T')[1].split(':')[0]+":"+ele.date.split('T')[1].split(':')[1]);
                pri.push(ele.priceUsd)
            })
           
            settime(ti);
            setprice(pri);
        })

    },[inputtime])

    function sethandler(e){
        setinputtime(e.value);
    }
    // console.log(window.innerWidth,window.innerHeight)

    if(window.innerHeight>window.innerWidth){
         const htmlele=document.querySelector('html');
         htmlele.style.height='100vh'
         htmlele.style.width='100vw'
        const Linegraphele=document.querySelector('.Linegraph');
        if(Linegraphele!=null){
        console.log(Linegraphele);
        Linegraphele.style.transform='rotate(90deg)';
        Linegraphele.style.height='100vw';
        Linegraphele.style.width='80vh';
        Linegraphele.style.margin='10vh 10vw 0 0';
        Linegraphele.style.padding='0'
    }
    }


    return (
        <div className='Linegraph'>
           
            <div className='head-container'>
            <Select options={options} id='Select' onChange={sethandler}/>
            <button onClick={backbutton}>Back</button>
            </div>
            <div className='graph-plot'>
                <Line data={data} />  
                <p>Line graphs</p>  
            </div>
         </div>
    );
}

export default Linegraph;