import { useEffect, useState } from 'react';
import './App.css';

import Cryptocoin from './componet/cryptocoin';
import Linegraph from './componet/Linegraph';

function App() {
  const [input,setinput]=useState('');
  const [coins,setcoins]=useState([]);
  const [pageno,setpageno]=useState(1);
  const [chart,setChart]=useState(false);
  const [coinname,setcoinname]=useState('bitcoin');
  const htmlele=document.querySelector('html');
  let rotat=false;

  useEffect(()=>{
    fetch('https://api.coincap.io/v2/assets')
    .then(response=>response.json())
    .then(data=>{setcoins(data.data)
    });
   },[])

   useEffect(()=>{
    if(pageno==1){
      console.log('page==1')
      document.querySelector('#previous').style.display='none';
    if(pagedata.length<20&&pagedata.length!=0){
      document.querySelector('#next').style.display='none';
    }
    }
    else if(pageno==filterdata.length/20){
      console.log('lastpage')
      document.querySelector('#next').style.display='none';
    }else{
      console.log(pageno,pagedata.length)
      // console.log('console.log page no',{pageno})
      document.querySelector('#previous').style.display='';
      document.querySelector('#next').style.display='';
      if(pagedata.length<20){
        document.querySelector('#next').style.display='none';
      }
    }}
  ,[pageno,input])



  const filterdata=coins.filter(coin=>{
    return coin.name.toLowerCase().includes(input.toLowerCase())
  })

const pagedata=[]
function pagedat(){
for(let i=0;i<filterdata.length;i++){
if(i<pageno*20&&i>=(pageno-1)*20){
  pagedata.push(filterdata[i])
}
}
}
function changeinput(e){
  setinput(e.target.value);
  console.log('input');
  setpageno(1)
  document.querySelector('#next').style.display='';
  
}  
pagedat()

let name=''
function coinClicked(e){
  name=e.target.parentElement.querySelector('#name').innerHTML.toLowerCase();
  setcoinname(name);
  setChart(!chart);
} 
function backbutton(){
  setChart(!chart);
  // htmlele.style.transform='rotate(90deg)';
  htmlele.style.transform='rotate(0)';
}

   return (
    <div className="App">
      {chart?(<Linegraph  backbutton={backbutton} name={coinname} />):(
        <div className="main-container">
        <div id="Search-container">
          <h1>Search a crypto-coin</h1>
          <input className='Search-input' type="text" placeholder='Search' onChange={(e)=>changeinput(e)}/>
        </div>
     
        <div id="coin-container">
          <div className='coin-container-head'>
           <div>Symbol</div>
           <div>Coin-Name</div>
           <div>Price</div>
           <div>24H Change</div>
           <div className='Market-Captial'>Market Capital</div>
          </div>
          <div className='coin-container-body' onClick={(e)=>coinClicked(e)}>
            {
            pagedata.map((coin,key)=>{
            (<div>called</div>)
            let price=Number(Number(coin.priceUsd).toPrecision(2)).toLocaleString();
            let marketcapital=Number(coin.marketCapUsd).toLocaleString();
            let changePercent24Hr=Number(coin.changePercent24Hr).toPrecision(2);
            return (
            <Cryptocoin  symbol={coin.symbol} name={coin.name} price={price} marketcapital={marketcapital} changePercent24Hr={changePercent24Hr} />)    
            })
          }
          </div>
        </div>
        <div className='footer'>
          <button  id='previous'onClick={()=>setpageno(pageno-1)}>Previous</button>
          <button id='next' onClick={()=>setpageno(pageno+1)}>Next</button>
        </div>
      </div>
      )}
    </div>
  );
}

export default App;
