import React from 'react';

function Cryptocoin(props) {
    const {symbol,name,price,marketcapital,changePercent24Hr}=props;
    
    return (
        <div className='Cryptocoin' key={name} >
                <div>{symbol}</div>
                <div id='name'>{name}</div>
                <div>${price}</div>
                {changePercent24Hr>0?(<div className='green'>{changePercent24Hr}%</div>):(<div className='red'>{changePercent24Hr}%</div>)}
                 <div className='Market-Captial'>{marketcapital}</div>
                   
        </div>
    );
}

export default Cryptocoin;